<template><section><h1>ImageryLayer</h1>
<p>The <code v-pre>vc-layer-imagery</code> component is used to load Cesium images. You can directly specify the <code v-pre>imageryProvider</code> of <code v-pre>vc-layer-imagery</code> to load the image, or you can mount the subcomponent <code v-pre>vc-provider-imager-xxx</code> to load the image.</p>
<h2>Example</h2>
<h3>Load an ImageryLayer to viewer</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
       <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
