<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>alpha</td>
<td>Number|function</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code>The alpha blending value of this layer, from 0.0 to 1.0. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the alpha is required, and it is expected to return the alpha value to use for the tile.</td>
</tr>
<tr>
<td>brightness</td>
<td>Number|function</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code>The brightness of this layer. 1.0 uses the unmodified imagery color. Less than 1.0 makes the imagery darker while greater than 1.0 makes it brighter. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the brightness is required, and it is expected to return the brightness value to use for the tile. The function is executed for every frame and for every tile, so it must be fast.</td>
</tr>
<tr>
<td>contrast</td>
<td>Number|function</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code>The contrast of this layer. 1.0 uses the unmodified imagery color. Less than 1.0 reduces the contrast while greater than 1.0 increases it. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the contrast is required, and it is expected to return the contrast value to use for the tile. The function is executed for every frame and for every tile, so it must be fast.</td>
</tr>
<tr>
<td>hue</td>
<td>Number|function</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code>The hue of this layer. 0.0 uses the unmodified imagery color. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the hue is required, and it is expected to return the contrast value to use for the tile. The function is executed for every frame and for every tile, so it must be fast.</td>
</tr>
<tr>
<td>saturation</td>
<td>Number|function</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code>The saturation of this layer. 1.0 uses the unmodified imagery color. Less than 1.0 reduces the saturation while greater than 1.0 increases it. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the saturation is required, and it is expected to return the contrast value to use for the tile. The function is executed for every frame and for every tile, so it must be fast.</td>
</tr>
<tr>
<td>gamma</td>
<td>Number|function</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code>The gamma correction to apply to this layer. 1.0 uses the unmodified imagery color. This can either be a simple number or a function with the signature function(frameState, layer, x, y, level). The function is passed the current frame state, this layer, and the x, y, and level coordinates of the imagery tile for which the gamma is required, and it is expected to return the gamma value to use for the tile. The function is executed for every frame and for every tile, so it must be fast.</td>
</tr>
<tr>
<td>splitDirection</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code>The ImagerySplitDirection split to apply to this layer.  <strong>LEFT: -1, NONE: 0, RIGHT: 1</strong></td>
</tr>
<tr>
<td>minificationFilter</td>
<td>Number</td>
<td><code v-pre>9729</code></td>
<td><code v-pre>optional</code> The texture minification filter to apply to this layer. Possible values are TextureMinificationFilter.LINEAR and TextureMinificationFilter.NEAREST. <strong>NEAREST: 9728, LINEAR: 9729, NEAREST_MIPMAP_NEAREST: 9984, LINEAR_MIPMAP_NEAREST: 9985, NEAREST_MIPMAP_LINEAR: 9986, NEAREST_MIPMAP_NEAREST: 9984</strong></td>
</tr>
<tr>
<td>magnificationFilter</td>
<td>Number</td>
<td><code v-pre>9729</code></td>
<td><code v-pre>optional</code> The texture minification filter to apply to this layer. Possible values are TextureMagnificationFilter.LINEAR and TextureMagnificationFilter.NEAREST. <strong>NEAREST: 9728, LINEAR: 9729</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> True if the layer is shown; otherwise, false.</td>
</tr>
<tr>
<td>maximumAnisotropy</td>
<td>Number</td>
<td>maximum supported</td>
<td><code v-pre>optional</code> The maximum anisotropy level to use for texture filtering. If this parameter is not specified, the maximum anisotropy supported by the WebGL stack will be used. Larger values make the imagery look better in horizon views.</td>
</tr>
<tr>
<td>minimumTerrainLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code>The minimum terrain level-of-detail at which to show this imagery layer, or undefined to show it at all levels. Level zero is the least-detailed level.</td>
</tr>
<tr>
<td>maximumTerrainLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code>The maximum terrain level-of-detail at which to show this imagery layer, or undefined to show it at all levels. Level zero is the least-detailed level.</td>
</tr>
<tr>
<td>cutoutRectangle</td>
<td>Rectangle</td>
<td></td>
<td><code v-pre>optional</code> Cartographic rectangle for cutting out a portion of this ImageryLayer.</td>
</tr>
<tr>
<td>colorToAlpha</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> Color to be used as alpha.</td>
</tr>
<tr>
<td>colorToAlphaThreshold</td>
<td>Number</td>
<td><code v-pre>0.004</code></td>
<td><code v-pre>optional</code> Threshold for color-to-alpha.</td>
</tr>
<tr>
<td>sortOrder</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> Relative order of layers.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ImageryLayer.html">ImageryLayer</a></strong></li>
</ul>
